import { IonItem, IonList, IonLoading, IonText } from "@ionic/react";
import Axios from "axios";
import { useEffect, useState } from "react";
import ReactPlayer from "react-player/youtube";
import {
  PushNotificationSchema,
  PushNotifications,
  Token,
  ActionPerformed,
} from "@capacitor/push-notifications";
import { AdMob } from "@capacitor-community/admob";
import "./video.css";

interface Video {
  _id: number;
  title: string;
  description: string;
  url: string;
  thumbnailUrl: string;
}
// thumbnailUrl: 'https://i.ytimg.com/vi/2/default.jpg',

const VideoList: React.FC = () => {
  const [videoData, setVideoData] = useState<Video[]>([]);
  const [showLoading, setShowLoading] = useState(false);
  const [selectedVideo, setSelectedVideo] = useState<Video | null>(null);

  const removeBanner = async () => {
    await AdMob.removeBanner();
  };

  useEffect(() => {
    PushNotifications.checkPermissions().then((res) => {
      if (res.receive !== "granted") {
        PushNotifications.requestPermissions().then((res) => {
          if (res.receive === "denied") {
          } else {
            register();
          }
        });
      } else {
        register();
      }
    });
  }, []);

  const register = () => {
    PushNotifications.register();
    PushNotifications.addListener("registration", (token: Token) => {});
    PushNotifications.addListener("registrationError", (error: any) => {
      alert("Error on registration: " + JSON.stringify(error));
    });
    PushNotifications.addListener(
      "pushNotificationReceived",
      (notification: PushNotificationSchema) => {}
    );
    PushNotifications.addListener(
      "pushNotificationActionPerformed",
      (notification: ActionPerformed) => {}
    );
  };

  useEffect(() => {
    setShowLoading(true);
    Axios.get("http://localhost:3000/video")
      .then((res: any) => {
        console.log(res.data);
        setVideoData(res.data);
        setShowLoading(false);
      })
      .catch((e) => {});
  }, []);

  // removeBanner();

  const handleVideoClick = (videoId: number) => {
    setSelectedVideo(videoData.find((video) => video._id === videoId) || null);
    removeBanner();
  };

  return (
    <>
      <div className="container">
        {selectedVideo && (
          <div className="video-player">
            <ReactPlayer
              controls={true}
              url={selectedVideo.url}
              playing={true}
              width="100%"
              height="80%"
            />
            <h2>{selectedVideo.title}</h2>
            <p>{selectedVideo.description}</p>
          </div>
        )}

        <IonLoading isOpen={showLoading} message={"Please wait..."} />
        <IonList className="video-list">
          {videoData.map((video: any, index) => {
            const { title, description, tags, thumbnailUrl } = video;
            return (
              <>
                <IonItem
                  className="video-list-item"
                  key={index}
                  onClick={() => handleVideoClick(video._id)}
                >
                  <img src={thumbnailUrl} alt={title} />
                  <IonText className="iontext">
                    <h3>{title}</h3>
                    <p>{description}</p>
                  </IonText>
                </IonItem>
              </>
            );
          })}
        </IonList>
      </div>
    </>
  );
};

export default VideoList;
